



























import Vue from 'vue';
import Component from 'vue-class-component';
import { Prop } from 'vue-property-decorator';

// Components
import HeaderNavigation from './HeaderNavigation.vue';

@Component({
  components: {
    HeaderNavigation
  }
})
export default class Header extends Vue {
  @Prop({
    type: Array,
    required: true
  })
  readonly routes!: [];
}
