



























































import Vue from 'vue';
import Component from 'vue-class-component';
import { required, email } from 'vuelidate/lib/validators';

@Component({
  metaInfo: {
    title: 'Login'
  },

  validations: {
    email: { required, email },
    password: { required }
  }
})
export default class Login extends Vue {
  private isSubmitting = false;

  private email = '';
  private password = '';

  get emailErrors() {
    const errors: string[] = [];
    if (this.$v.email && this.$v.email.$dirty) {
      !this.$v.email.required && errors.push('Please enter your email!');
      !this.$v.email.email &&
        errors.push('Entered email address is not a valid email address!');
    }
    return errors;
  }
  get passwordErrors() {
    const errors: string[] = [];
    if (this.$v.password && this.$v.password.$dirty) {
      !this.$v.password.required && errors.push('Please enter your password!');
    }
    return errors;
  }

  async submit() {
    this.$v.$touch();
    if (this.$v.$pending || this.$v.$error) {
      return;
    }

    this.isSubmitting = true;
    try {
      await this.$store.dispatch('login', {
        email: this.email,
        password: this.password
      });

      this.$router.push('/');
    } catch (error) {
      console.error(error);
    }
    this.isSubmitting = false;
  }
}
